// SPACE via CodePen

.stars, .twinkling, .clouds {
    position: absolute;
    position:fixed;
    display: block;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    width: 100%;
    // z-index: 0;
}

.stars {
    z-index: -3;
    background: #000 url("../images/stars/stars.png") repeat top center;
}

.twinkling {
    z-index: -2;
    background: transparent url("../images/stars/twinkling.png") repeat top center;
    animation: move-twink-back 200s linear infinite;
}

// .clouds {
